.Footer {
  height: 355px;
  background-color: #383838;
}

.footerContent {
  padding-top: 53px;
  display: flex;
  justify-content: space-between;
  font-family: "PoetsenOne", arial;
}
.centerInfo {
  margin-left: 50px;
  display: flex;
  gap: 65px;
}

.centerInfoChild {
  display: flex;
  flex-direction: column;
}
.centerInfoChild h2 {
  font-size: 20px;
  margin-bottom: 24px;
}
.centerInfoChild a {
  margin-bottom: 12px;
  font-family: "Play", sans-serif;
  font-size: 16px;
}
.footerContent h3 {
  font-size: 18px;
  font-family: "Play", sans-serif;
  opacity: 50%;
  font-weight: 100;
  margin-top: 12px;
}
.footerLine {
  width: max-content;
  margin: 30px auto 0 auto;
}

.termsAndPrivatPolice {
  font-size: 16px;
  margin: 23px 100px 0 0;
  display: flex;
  gap: 50px;
  justify-content: center;
  align-items: center;
  opacity: 50%;
  font-family: "Play", sans-serif;
}
.span {
  display: flex;
  opacity: 50%;
  font-family: "Play", sans-serif;
  font-size: 14px;
  color: #d8d8d8;
  justify-content: center;
  margin: 5px 50px 12px 0;
}

@media (max-width: 1280px) {
  .footerLine img {
    max-width: 956px;
  }
}

@media (max-width: 1000px) {
  .footerLine img {
    max-width: 900px;
  }
}

@media (max-width: 940px) {
  .footerLine img {
    display: none;
  }
}
@media (max-width: 860px) {
  .centerInfo {
    margin-left: 50px;
    display: flex;
    gap: 60px;
  }
  .centerInfoChild h2 {
    font-size: 16px;
    margin-bottom: 24px;
  }
  .logo img {
    max-width: 122px;
  }
  .footerContent h3 {
    font-size: 14px;
  }
  .centerInfoChild a {
    margin-bottom: 10px;
  }
  .termsAndPrivatPolice {
    font-size: 14px;
    margin: 41px 0 0 0;
    gap: 24px;
  }
  .span {
    font-family: "Play", sans-serif;
    margin: 15px 0 12px 0;
  }
}
@media (max-width: 720px) {
  .footerContent {
    padding-top: 28px;
    align-items: center;
    flex-direction: column;
  }
  .Footer {
    height: 560px;
  }
  .footerContent h3 {
    display: none;
  }
  .centerInfo {
    width: 100%;
    margin-left: 0;
    gap: 120px;
    margin-top: 31px;
    margin-bottom: 33px;
  }
}

@media (max-width: 420px) {
  .span {
    margin: 5px 0 0 0;
  }
}
