.presentText {
  padding-top: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "PoetsenOne", arial;
}
.presentText h2 {
  font-size: 32px;
  line-height: 48px;
}
.buttons {
  margin-top: 32px;
  display: flex;
  gap: 46px;
}

.marketplace {
  font-size: 16px;
  letter-spacing: 2px;
  padding: 12px 38px 10px 38px;
  margin-top: 6px;
  background-repeat: no-repeat;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='45px' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='45px' fill='none' rx='20' ry='20' stroke='%23FFF505FF' stroke-width='4' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='7' stroke-linecap='butt'/%3e%3c/svg%3e");
}

.roadMapBack {
  padding-bottom: 100px;
}

.roadMapHeader {
  font-size: 40px;
  margin: 80px auto 0 auto;
  max-width: max-content;
  letter-spacing: 2px;
}
.roadMapHeader span {
  color: #f15641;
}

.roadMap {
  position: relative;
  margin: 125px auto 0;
  max-width: max-content;
}

.q3e22,
.q4e22,
.q1e23,
.q2e23,
.q3e23,
.q4e23 {
  max-width: 200px;
}

.qTop,
.qBottom {
  position: absolute;
  font-family: "PoetsenOne", arial;
  z-index: 7;
  display: flex;
  gap: 120px;
}

.foneRound {
  opacity: 80%;
  z-index: 5;
  top: -30%;
  right: 0;
  left: 0;
  margin: 0 auto;
  position: absolute;
  width: 582px;
  height: 582px;
  background-image: url("data:image/svg+xml,%3csvg width='582px' height='582px' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='50%' ry='50%' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='5' stroke-linecap='square'/%3e%3c/svg%3e");
}
.roadMap div ul li {
  position: relative;
  z-index: 10;
  list-style-type: none;
  font-family: "Play", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 200;
}

.roadMap div h3 {
  font-size: 32px;
  line-height: 38px;
  margin-bottom: 8px;
}

.qTop {
  left: 2.5%;
  top: -4%;
}
.qBottom {
  right: 2.5%;
  bottom: -20%;
}

.q3e22,
.q4e22,
.q1e23 {
  text-align: end;
}

.q3e22,
.q4e22,
.q2e23 {
  color: #fff505;
}
.ButtonForNftCollection {
  display: none;
}

@media (max-width: 1240px) {
  .roadMap img {
    max-width: 955px;
  }
  .roadMap div ul li {
    font-size: 16px;
  }
  .qBottom {
    gap: 80px;
    right: 0;
  }
  .qTop {
    gap: 80px;
    left: 1%;
  }
  .foneRound {
    margin: 60px auto 0;
  }
}
@media (max-width: 998px) {
  .roadMap img {
    max-width: 686px;
  }
  .roadMap div ul li {
    font-size: 14px;
  }
  .roadMap div h3 {
    font-size: 24px;
    line-height: 28px;
  }
  .qBottom {
    gap: 35px;
    right: -3%;
  }
  .qTop {
    gap: 35px;
    left: -2%;
  }
  .foneRound {
    width: 352px;
    height: 352px;
    background-image: url("data:image/svg+xml,%3csvg width='352px' height='352px' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='50%' ry='50%' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='5' stroke-linecap='square'/%3e%3c/svg%3e");
  }
}
@media (max-width: 800px) {
  .presentText h2 {
    font-size: 24px;
    line-height: 40px;
  }
  .marketplace {
    font-size: 14px;
    margin-top: 3px;
  }
  .roadMapHeader {
    font-size: 24px;
    margin: 64px auto 0 auto;
  }
  .roadMap {
    margin: 78px auto 0;
  }
}

@media (max-width: 580px) {
  .presentText {
    padding-top: 10px;
  }
  .presentText h2 {
    font-size: 16px;
    line-height: 24px;
  }
  .roadMapHeader {
    font-size: 24px;
    margin: 40px auto 0 auto;
  }
}
@media (max-width: 730px) {
  .qTop,
  .qBottom {
    flex-direction: column;
    top: 5%;
    gap: 85px;
  }
  .qTop {
    left: -92%;
  }
  .qBottom {
    right: -92%;
    top: 18%;
  }
  .foneRound {
    top: 8%;
    left: -70%;
  }
  .roadMap {
    margin: 40px auto 0;
  }
  .roadMapBack {
    padding-bottom: 0;
  }
}

@media (max-width: 420px) {
  .Button {
    display: none;
  }
  .marketplace {
    background-size: contain;
    padding: 14px 40px;
    margin-top: 0;
  }
  .buttons {
    margin-top: 24px;
  }
  .presentText h2 {
    font-size: 15px;
    line-height: 20px;
  }
  .presentText {
    padding-top: 0;
  }
  .ButtonForNftCollection {
    display: block;
    margin-bottom: 20px;
  }
  .roadMap div ul li {
    font-size: 10px;
    max-width: 100px;
  }
  .roadMap div h3 {
    font-size: 14px;
    line-height: 28px;
  }
  .qTop,
  .qBottom {
    gap: 100px;
  }
  .qTop {
    left: -65%;
    top: 6%;
  }
  .qBottom {
    right: -65%;
    top: 19%;
  }
  .foneRound {
    width: 315px;
    height: 315px;
    background-image: url("data:image/svg+xml,%3csvg width='315px' height='315px' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='50%' ry='50%' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='5' stroke-linecap='square'/%3e%3c/svg%3e");
  }
  .foneRound {
    top: 10%;
    left: -57.5%;
  }
}
