.nftCollection {
  padding-top: 80px;
}

.banner {
  height: 160px;
  border-radius: 30px;
  background-color: #f15641;
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner span {
  color: #fff505;
}
.banner span,
.banner h2 {
  font-size: 40px;
  letter-spacing: 3px;
}

@media (max-width: 1000px) {
  .banner {
    height: 120px;
  }
}

@media (max-width: 730px) {
  .banner span,
  .banner h2 {
    font-size: 24px;
  }
}
@media (max-width: 480px) {
  .banner {
    height: 80px;
  }
  .nftCollection {
    padding-top: 40px;
  }
}
@media (max-width: 460px) {
  .banner {
    height: 60px;
  }
  .banner span,
  .banner h2 {
    font-size: 20px;
  }
  .nftCollection {
    padding-top: 32px;
  }
}
