.formic {
  color: rgb(212, 41, 41);
  position: relative;
}
.formic h4 {
  position: absolute;
  top: 15%;
}

.formic h2 {
  font-size: 20px;
  margin-bottom: 24px;
  font-weight: 100;
  color: white;
}
.forInputAndButton {
  position: relative;
}
.emailInput {
  height: 40px;
  width: 256px;
  border-radius: 30px;
  padding-left: 20px;
}
.submit {
  position: absolute;
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: black;
  color: white;
  border: none;
  right: 0;
}
.links {
  display: flex;
  margin-top: 24px;
  align-items: center;
  gap: 16px;
}
.forInputAndButton h4 {
  position: absolute;
}
.loading img {
  max-width: 40px;
}
.loading {
  position: absolute;
  left: -20%;
  top: 0;
}

@media (max-width: 860px) {
  .emailInput {
    width: 189px;
    font-size: 12px;
    padding-left: 5px;
  }
  .submit {
    right: -5%;
  }
  .formic h2 {
    font-size: 16px;
  }
  .links {
    margin-top: 18px;
    gap: 10px;
  }
  .links img {
    max-width: 37px;
  }
  .loading {
    left: -30%;
  }
}

@media (max-width: 720px) {
  .emailInput {
    width: 331px;
    font-size: 12px;
    padding-left: 5px;
  }
  .formic {
    text-align: center;
  }
  .links {
    display: flex;
    justify-content: center;
    gap: 15px;
  }
  .loading {
    left: -20%;
  }
  .links img {
    max-width: 45px;
  }
}
@media (max-width: 400px) {
  .emailInput {
    width: 320px;
  }
}
@media (max-width: 440px) {
  .emailInput {
    width: 270px;
  }
}
@media (max-width: 355px) {
  .emailInput {
    width: 250px;
  }
}
@media (max-width: 480px) {
  .loading {
    left: -15%;
  }
}
