.sectionNftContent {
  overflow: hidden;
}
.nftCardContent {
  display: flex;
  padding-top: 80px;
  margin: 0 auto 0 auto;
  gap: 30px;
  animation: 14s scrollX infinite linear;
}
.scrollX,
.scrollX2 {
  display: flex;
  justify-content: space-between;
  max-width: max-content;
  gap: 30px;
}

@keyframes scrollX {
  0% {
    transform: translateX(-2177px);
  }
  100% {
    transform: translateX(0);
  }
}

.nftCardContent img,
.nftCardContent2 img {
  max-width: fit-content;
}
.nftCardContent::-webkit-scrollbar {
  width: 0;
}
.nftCardContent2::-webkit-scrollbar {
  width: 0;
}
.sectionNftContent::-webkit-scrollbar {
  width: 0;
}

@media (max-width: 480px) {
  .nftCardContent,
  .nftCardContent2 {
    padding-top: 40px;
  }
}
