@media (max-width: 768px) {
  .header-content {
    margin-top: 52px;
  }
  .h1-content {
    margin-top: 15px;
  }
  .header-image img {
    max-width: 126px;
  }
  .h1-content h2 {
    font-size: 40px;
    line-height: 34px;
  }
  .h1-content h2:nth-child(2) {
    margin-left: 100px;
  }
  .h1-content h2:nth-child(3) {
    margin-left: 180px;
  }
  .header-phone img {
    max-width: 208px;
  }
  .h1 {
    font-size: 20px;
    margin-top: 24px;
    line-height: 29px;
  }
  header {
    background-size: 150%;
  }
  .header-footer {
    height: 100px;
    bottom: 20px;
    padding: 10px 5px 28px 20px;
    bottom: 3px;
  }
  .header-footer h2 {
    font-size: 16px;
    margin-bottom: 7px;
  }
  .header-footer p {
    font-size: 12px;
  }
  .header-footer-social {
    max-width: 230px;
  }
  .header-footer-nft {
    max-width: 220px;
  }
  .header-footer-solana {
    max-width: 230px;
  }
  .bollSmall {
    max-width: 48px;
    top: 2%;
    left: 50%;
  }
  .bollLarge {
    max-width: 66px;
    top: 5%;
    right: -5%;
  }
}
