@import url("https://fonts.cdnfonts.com/css/poetsenone");

header {
  background-image: url("Header-image/Header-fone.png");
  background-repeat: no-repeat;
  background-position: center;
  background-position-y: 0;
  position: relative;
}

.for-all-header {
  position: relative;
  max-width: 1280px;
  padding: 0 40px;
  margin: 0 auto;
}

.header-start {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navBar-menu {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
  gap: 32px
}

.navBar-menu a {
  font-family: "PoetsenOne", arial;
  text-decoration: none;
  color: white;
  font-size: 16px;
}

.whitepaper {
  font-size: 14px;
  letter-spacing: 2px;
  padding: 11px 35px;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='15' ry='15' stroke='%23FFF505FF' stroke-width='4' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='7' stroke-linecap='butt'/%3e%3c/svg%3e");
}

.small-menu {
  width: 38px;
  height: 38px;
  display: none;
  z-index: 11;
}

.header-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-top: 106px;
  color: white;
}
.h1-content {
  margin-top: 39px;
}
.h1-content h2 {
  font-size: 56px;
  letter-spacing: 5px;
  line-height: 52px;
}

.h1-content h2:nth-child(1) {
  margin-left: 27px;
}
.h1-content h2:nth-child(2) {
  margin-left: 124px;
}
.h1-content h2:nth-child(3) {
  margin-left: 160px;
  color: #fff505;
}
.h1-content h2:nth-child(4) {
  color: #f15641;
}
.china-text {
  position: absolute;
  top: 16%;
  left: 37%;
  transform: rotate(90deg);
}
.h1 {
  font-size: 32px;
  line-height: 48px;
  font-family: "PoetsenOne", arial;
  max-width: 350px;
  margin-top: 60px;
  letter-spacing: 2px;
}

.buttonAndButton {
  position: relative;
  z-index: 10;
  font-family: "PoetsenOne", arial;
  font-size: 14px;
  display: flex;
  max-width: 384px;
  justify-content: space-between;
  align-items: center;
}
.buttonAndButton a:nth-child(2) {
  display: none;
}
.forButton {
  margin-top: 96px;
}
.buttonAndButton div:nth-child(4) {
  padding: 10px 15px 5px 15px;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='15' ry='15' stroke='%23FFF505FF' stroke-width='4' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='7' stroke-linecap='butt'/%3e%3c/svg%3e");
  border-radius: 10px;
  margin-bottom: 5px;
}

.buttonAndButton span {
  margin-left: 20px;
  letter-spacing: 3px;
}

.header-phone {
  margin-left: 60px;
}
.header-phone img {
  position: relative;
  z-index: 3;
}
.header-phone img:nth-child(1) {
  display: none;
}

.scroll-parents {
  width: 30px;
  position: absolute;
  right: 5%;
  bottom: 34%;
  cursor: pointer;
  transition: 0.5s;
  z-index: 5;
}

.bottom {
  bottom: 15%;
}

.scroll {
  position: fixed;
  max-width: inherit;
  transition: 0.5s;
}
.scroll-icon {
  z-index: 5;
  transform: rotate(180deg);
}
.scroll img {
  position: relative;
  display: inline-block;
  z-index: 5;
  cursor: pointer;
}

.header-footer {
  position: relative;
  z-index: 4;
  color: black;
  display: flex;
  gap: 2vw;
  font-family: "PoetsenOne", arial;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  padding: 28px 5px 28px 20px;
  width: 100%;
  bottom: 40px;
  height: 161px;
  background-color: #fff505;
  border-radius: 30px;
}

.absolutle-icons img {
  max-width: 100%;
}

.absolutle-icons {
  position: absolute;
  top: 3%;
  left: 18%;
}

.header-footer p {
  font-family: "Play", sans-serif;
  font-size: 18px;
}
.header-footer h2 {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 400;
}

.bollSmall,
.bollLarge {
  position: absolute;
}
.bollSmall {
  top: 2%;
  left: 41%;
}
.bollLarge {
  top: 5%;
  right: 1%;
}

.header-footer-social,
.header-footer-nft,
.header-footer-solana {
  display: flex;
  margin-bottom: auto;
  gap: 20px;
}
.header-footer-nft {
  max-width: 350px;
}
.header-footer-solana {
  max-width: 370px;
}
