@media (max-width: 650px) {
  header {
    background-image: url("../Header-image/Header-fone-small.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: -30px;
  }
  .for-all-header {
    padding: 0 20px;
  }
  .header-image img {
    max-width: 93px;
  }
  .header-content {
    margin-top: 136px;
    justify-content: flex-start;
  }
  .h1-content h2 {
    font-size: 32px;
    line-height: 34px;
    letter-spacing: 2px;
  }
  .h1-content h2:nth-child(2) {
    margin-left: 45px;
  }
  .h1-content h2:nth-child(3) {
    margin-left: 70px;
  }
  .h1-content h2:nth-child(4) {
    margin-left: 25px;
  }
  .header-phone {
    margin-left: 0;
  }
  .h1 {
    font-size: 18px;
    margin-top: 32px;
    line-height: 32px;
    max-width: 300px;
  }
  .buttonAndButton div:nth-child(4) {
    display: none;
  }
  .buttonAndButton span {
    display: none;
  }
  .china-text {
    font-size: 10px;
    top: 34%;
    left: 41%;
  }
  .bollSmall {
    top: -10%;
    left: 37%;
  }
  .bollLarge {
    max-width: 43px;
    top: -15%;
    right: 0;
  }
  .header-footer {
    flex-direction: column;
    align-items: flex-start;
    height: 248px;
    padding: 15px 5px 28px 20px;
    bottom: 3px;
  }
  .absolutle-icons {
    display: none;
  }
  .header-footer-social {
    max-width: none;
  }
  .header-footer-nft {
    max-width: none;
  }
  .header-footer-solana {
    max-width: none;
    align-items: center;
  }
  .header-footer-solana img {
    max-width: 61px;
  }
  .header-footer-solana-description {
    margin-bottom: 15px;
  }
}

@media (max-width:570px){
  .header-phone{
    position: absolute;
    top: 0;
    right: 0;
  }
  .header-footer {
    bottom: -70px;
  }
  .header-phone img:nth-child(1){
    display: block;
  }
  .header-phone img:nth-child(2){
    display: none;
  }
}