.sectionOurTeam {
  padding-top: 150px;
}

.sectionOurTeam h2 {
  font-size: 40px;
  max-width: max-content;
  letter-spacing: 3px;
  margin: 0 auto 85px auto;
}

.sectionOurTeam span {
  color: #f15641;
}
.aboutTeamMembers {
  position: relative;
  z-index: 2;
  height: 632px;
  background-image: url("OurTeam-image/team-fone.png");
}

.content {
  display: flex;
  height: 100%;
  gap: 78px;
  justify-content: center;
  align-items: center;
}
@media (max-width: 840px) {
  .content {
    gap: 20px;
  }
  .sectionOurTeam h2 {
    font-size: 24px;
  }
}

@media (max-width: 768px) {
  .sectionOurTeam {
    padding-top: 64px;
  }

  .aboutTeamMembers {
    height: 398px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .sectionOurTeam h2 {
    margin: 0 auto 54px auto;
  }
}

@media (max-width: 730px) {
  .content {
    flex-direction: column;
  }
  .aboutTeamMembers {
    height: max-content;
  }
  .sectionOurTeam {
    padding-top: 0;
  }
  .content {
    padding: 40px;
  }
}
