@media (max-width: 1050px) {
  .header-phone img {
    max-width: 265px;
  }
  .navBar-menu div:nth-child(1),
  .navBar-menu div:nth-child(2),
  .navBar-menu div:nth-child(3),
  .navBar-menu div:nth-child(4) {
    display: none;
  }
  .header-content {
    margin-top: 75px;
  }
  .navBar-menu {
    align-items: center;
    margin-top: 5px;
  }
  .small-menu {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 3px 1px;
  }
  .small-menu span {
    height: 3px;
    width: 100%;
    display: block;
    background-color: aliceblue;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: 0.7s;
  }

  .chaildOne {
    transform: rotate(25deg);
    max-width: 25px;
    margin: 8px 0 0 12px;
  }
  .chaildThree {
    transform: rotate(-25deg);
    max-width: 25px;
    margin: 0 0 7px 12px;
  }
  .h1-content h2 {
    position: relative;
    z-index: 5;
    font-size: 44px;
    line-height: 38px;
    max-width: 30px;
  }
  .h1-content h2:nth-child(1) {
    margin-left: 0;
  }
  .h1-content h2:nth-child(4) {
    margin-left: 57px;
  }
  header {
    background-size: 141%;
  }
  .h1 {
    font-size: 24px;
    margin-top: 32px;
    line-height: 34px;
  }
  .forButton {
    margin-top: 36px;
  }
  .china-text {
    font-size: 12px;
    top: 14%;
    left: 45%;
  }
  .header-footer {
    height: 120px;
    bottom: 20px;
    padding: 10px 50px 28px 20px;
  }
  .icon {
    max-width: 60px;
  }
  .header-footer h2 {
    font-size: 18px;
    margin-bottom: 7px;
  }
  .header-footer p {
    font-size: 16px;
  }
  .header-footer-social,
  .header-footer-nft,
  .header-footer-solana {
    gap: 15px;
  }
  .header-footer-nft {
    max-width: 250px;
  }
  .header-footer-solana {
    max-width: 250px;
  }
  .header-footer-social {
    max-width: 250px;
  }
  .bollSmall {
    max-width: 62px;
    top: 2%;
    left: 49%;
  }
  .bollLarge {
    max-width: 98px;
    top: 5%;
    right: 1%;
  }
  .scroll {
    display: none;
  }
}
