.BeforeFooter {
  height: 450px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.fone {
  z-index: -5;
  max-width: fit-content;
  position: fixed;
  bottom: 0;
}

.eventContent {
  width: 528px;
  height: 290px;
  background-color: #383838;
  border-radius: 35px;
  font-family: "PoetsenOne", arial;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.eventContent h2 {
  font-size: 32px;
  color: #fff505;
  font-weight: 100px;
  letter-spacing: 2px;
  margin-bottom: 24px;
}
.eventContent p {
  font-size: 20px;
  font-weight: 100px;
}

.eventContent p span {
  color: #f15641;
}
.eventContent p:nth-child(3) {
  margin-bottom: 40px;
}

@media (max-width: 768px) {
  .BeforeFooter {
    height: 326px;
  }
  .eventContent {
    width: 312px;
    height: 198px;
  }
  .eventContent h2 {
    font-size: 24px;
    margin-bottom: 24px;
  }
  .eventContent p {
    font-size: 16px;
  }
  .eventContent p:nth-child(3) {
    margin-bottom: 24px;
  }
}
