.card {
  padding: 11px;
  background-color: #383838;
  border-radius: 20px;
  width: max-content;
}

.cardImg {
  text-align: center;
}

.nameAndRar {
  margin-top: 17px;
  font-family: "PoetsenOne", arial;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nameAndRar h2,
.nameAndRar span {
  font-size: 14px;
  letter-spacing: 3px;
}

.nameAndRar span {
  padding: 1px 9px 2px 9px;
  border-radius: 3px;
  background-color: #f15641;
}

.lvlAndMint {
  margin-top: 10px;
  font-family: "Play", sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.lvlAndMint h3,
.lvlAndMint h4 {
  font-size: 16px;
  font-weight: 200;
  color: #d8d8d8;
}
.lvlAndMint h4 {
  margin-right: 27px;
}
.solanaPriceAndBuy {
  margin-top: 36px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Play", sans-serif;
}
.card button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 14px;
  color: white;
  font-weight: 700;
  padding: 8px 32px;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='20' ry='20' stroke='%23FFF505FF' stroke-width='3' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='0' stroke-linecap='butt'/%3e%3c/svg%3e");
}
.solanaPriceAndBuy h2 {
  font-weight: 100;
  font-family: "Orbitron", sans-serif;
  font-size: 18px;
  margin-right: 20px;
}
