.firstContent {
  display: flex;
  margin: 0 0 140px 107px;
  gap: 60px;
  padding-top: 100px;
}

.gameConceptDescription {
  max-width: 531px;
  position: relative;
}
.gameConceptHeader,
.gameConceptHeaderSpan {
  font-size: 40px;
  letter-spacing: 5px;
  margin: 0 0 24px 0;
  z-index: 4;
  position: relative;
}
.gameConceptHeaderSpan {
  color: #fff505;
}

.gameConceptDescriptionBorder {
  position: absolute;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='14' stroke-linecap='square'/%3e%3c/svg%3e");
  width: 465px;
  height: 206px;
  transform: rotate(10deg);
  z-index: 1;
}

.lapa {
  opacity: 80%;
  transform: rotate(10deg);
  width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 210px;
  height: 180px;
  background-repeat: no-repeat;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='%23FFF505FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='14' stroke-linecap='square'/%3e%3c/svg%3e");
}
.lapa img {
  transform: rotate(-17deg);
}

.image {
  margin-top: 20px;
}

.secondContent {
  display: flex;
  font-family: "PoetsenOne", arial;
}

.descriptions {
  position: relative;
  margin-left: 106px;
  max-width: 250px;
}

.descriptions h2 {
  font-size: 32px;
  max-width: 170px;
  margin-bottom: 24px;
  z-index: 10;
  position: relative;
}
.foneFirst,
.foneSecond {
  opacity: 80%;
  z-index: 1;
  left: 10%;
  position: absolute;
  width: 220px;
  height: 204px;
  border-radius: 30px;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='%23F15641FF' stroke-width='3' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='40' stroke-linecap='square'/%3e%3c/svg%3e");
  transform: rotate(10deg);
}

.foneSecond {
  background-color: #f15641;
  left: 6%;
}

.secondDescription {
  position: relative;
  margin: 120px 0 0 50px;
}

.thirdContent {
  font-family: "PoetsenOne", arial;
  margin: 120px 0 0 91px;
  display: flex;
  justify-content: space-between;
}
.descriptionsTwo {
  position: relative;
}
.descriptionsTwoAfter {
  display: none;
}

.descriptionsTwoH2 {
  font-size: 32px;
  max-width: 170px;
  margin-bottom: 24px;
}

.p {
  max-width: 250px;
}

.image2 {
  margin-top: 50px;
}

.lapa2 {
  opacity: 80%;
  margin: 90px 0 0 97px;
  transform: rotate(10deg);
  padding: 55px 75px;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='5' stroke-linecap='square'/%3e%3c/svg%3e");
}
.connectWithPlayersAfter {
  display: none;
}

@media (max-width: 1160px) {
  .firstContent {
    margin: 0 0 140px 0;
  }
  .image img {
    max-width: 620px;
  }
  .descriptions {
    margin-left: 72px;
  }
  .secondDescription {
    margin: 80px 0 0 0;
  }
  .lapa {
    width: 196px;
    height: 152px;
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='%23FFF505FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
  }
  .thirdContent {
    margin: 60px 0 0 0;
  }
  .lapa2 {
    margin: 90px 0 0 0;
  }
  .image2 {
    margin-top: 0;
  }

  .image2 img {
    max-width: 625px;
    margin-left: 75px;
  }
}

@media (max-width: 990px) {
  .gameConceptHeader,
  .gameConceptHeaderSpan {
    font-size: 32px;
  }
  .gameConceptDescriptionBorder {
    max-width: 362px;
  }
  .gameConceptDescription {
    max-width: 350px;
  }

  .firstContent {
    gap: 0;
    justify-content: space-between;
    margin: 0 0 80px 0;
  }
  .connectWithPlayers {
    display: none;
  }
  .lapa {
    display: none;
  }
  .connectWithPlayersAfter {
    display: block;
    position: relative;
    font-family: "PoetsenOne", arial;
    max-width: 340px;
  }
  .connectWithPlayersAfter h2 {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 24px;
    position: relative;
    z-index: 2;
  }
  .descriptionsTwo {
    display: none;
  }
  .descriptionsTwoAfter {
    display: block;
    max-width: 300px;
  }
  .secondDescription {
    max-width: 300px;
  }
  .lapa2 {
    display: none;
  }
  .secondContent {
    flex-direction: column;
  }
  .descriptions {
    display: flex;
    justify-content: space-between;
    max-width: 100%;
    margin-left: 0;
    margin-top: 50px;
  }
  .image img {
    max-width: 100%;
  }
  .image {
    max-width: max-content;
    margin: 0 auto;
  }
  .image2 {
    max-width: max-content;
    margin: 0 auto;
  }
  .image2 img {
    max-width: 100%;
    margin-left: 0;
  }
}

@media (max-width: 768px) {
  .firstContent {
    gap: 15vw;
  }
  .foneFirst,
  .foneSecond {
    width: 200px;
    height: 180px;
  }
  .connectWithPlayersAfter {
    margin-top: 50px;
  }
}

@media (max-width: 740px) {
  .connectWithPlayersAfter {
    position: absolute;
    top: 10%;
    right: 0;
    text-align: end;
    padding-right: 40px;
  }

  .firstContent {
    margin: 0 0 0 0;
    padding-top: 100px;
  }
  .gameConceptDescription {
    max-width: 300px;
    padding-bottom: 100px;
  }
  .gameConceptDescriptionBorder {
    max-width: 100%;
    height: 100%;
  }
}
@media (max-width: 650px) {
  .firstContent {
    justify-content: center;
  }
  .gameConceptDescription {
    max-width: none;
  }
  .gameConceptDescriptionBorder {
    background-image: url("./GameConcept-Image/game-concept-border.png");
    background-repeat: no-repeat;
    transform: rotate(0);
  }
  .connectWithPlayersAfter {
    display: none;
  }
  .firstContent {
    padding-top: 50px;
  }
  .connectWithPlayers {
    display: block;
  }
  .descriptions {
    display: grid;
    grid-template-areas:
      "h n"
      "c c";
    gap: 3vh;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
  }
  .connectWithPlayers {
    grid-area: h;
  }

  .descriptionsTwoAfter {
    grid-area: c;
    max-width: 100%;
    text-align: center;
    margin-top: 80px;
  }
  .secondDescription {
    grid-area: n;
    margin: 20px 0 0 0;
  }
  .p {
    max-width: 100%;
  }
  .descriptions h2 {
    max-width: none;
    font-size: 24px;
    margin-bottom: 16px;
  }
  .connectWithPlayers h2 {
    max-width: 140px;
  }
  .descriptionsTwoH2 {
    max-width: max-content;
    justify-content: center;
    display: flex;
    gap: 1vw;
  }
  .foneSecond {
    width: 186px;
    height: 140px;
    left: 15%;
  }
  .foneFirst {
    height: 160px;
    left: 0;
  }
  .descriptionsTwoAfter .foneFirst {
    background-image: url("./GameConcept-Image/walk-border.png");
    background-size: contain;
    transform: none;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    top: 65%;
  }
}@media (max-width: 570px){
  .firstContent{
    padding-top: 100px;
  }
}
@media (max-width: 500px) {
  .descriptionsTwoAfter {
    margin-top: 60px;
  }
}

@media (max-width: 470px) {
  .gameConceptDescriptionBorder {
    background-image: url("./GameConcept-Image/game-concept-border360.png");
  }

  .connectWithPlayers .foneFirst {
    display: none;
  }
  .foneSecond {
    width: 142px;
    height: 105px;
    left: 5%;
  }
}

@media (max-width: 430px) {
  .descriptionsTwoAfter .foneFirst {
    background-image: url("./GameConcept-Image/walk-border360.png");
    background-size: contain;
  }
}

@media (max-width: 360px) {
  .gameConceptDescription {
    padding-bottom: 60px;
  }
  .descriptions {
    margin-top: 30px;
  }
  .descriptionsTwoAfter {
    margin-top: 30px;
  }
  .firstContent {
    padding-top: 90px;
  }
  .gameConceptHeader {
    letter-spacing: 2px;
    margin: 0 0 15px 0;
  }
}
