@media (max-width: 470px) {
  .china-text {
    display: none;
  }
  .whitepaper {
    display: none;
  }
}
@media (max-width: 450px) {
  .header-content {
    margin-top: 62px;
  }
  .h1-content h2 {
    font-size: 28px;
    line-height: 30px;
    letter-spacing: 2px;
  }
  .h1-content h2:nth-child(2) {
    margin-left: 50px;
  }
  .h1-content h2:nth-child(3) {
    margin-left: 20px;
  }
  .h1-content h2:nth-child(4) {
    margin-left: 5px;
  }
  .header-phone img {
    max-width: 165px;
  }
  .h1 {
    font-size: 14px;
    line-height: 20px;
    max-width: 200px;
    letter-spacing: 1px;
  }
  .buttonAndButton div:nth-child(4) {
    display: block;
  }
  .buttonAndButton span {
    display: block;
  }
  .buttonAndButton div:nth-child(1) {
    display: none;
  }
  .buttonAndButton a:nth-child(2) {
    display: block;
  }

  .buttonAndButton span {
    display: none;
  }
  .buttonAndButton a {
    font-size: 12px;
  }
  .buttonAndButton {
    justify-content: flex-start;
    gap: 15px;
    max-width: 130px;
  }
  .forButton {
    margin-top: 32px;
  }
  .header-footer {
    padding: 15px 5px 28px 10px;
    bottom: -65px;
  }
}
