.avatar {
  width: 275px;
  height: 275px;
  border-radius: 50%;
  background-image: url("data:image/svg+xml,%3csvg width='275px' height='275px' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='275px' height='275px' fill='none' rx='50%' ry='50%' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='1' stroke-linecap='square'/%3e%3c/svg%3e");
}
.avatar {
  overflow: hidden;
}
.avatar img {
  max-width: fit-content;
  position: relative;
  z-index: -1;
}

.TeamMember h2 {
  max-width: 100%;
  margin: 32px 0 0 0;
  font-family: "PoetsenOne", arial;
}

.TeamMember {
  text-align: center;
  font-size: 32px;
  margin-bottom: 40px;
}

.TeamMember span {
  color: #f15641;
}

@media (max-width: 990px) {
  .avatar {
    width: 230px;
    height: 230px;
    background-image: url("data:image/svg+xml,%3csvg width='230px' height='230px' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='230px' height='230px' fill='none' rx='50%' ry='50%' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='1' stroke-linecap='square'/%3e%3c/svg%3e");
  }
  .avatar img {
    max-width: 237px;
  }
}

@media (max-width: 730px) {
  .avatar {
    width: 304px;
    height: 304px;
    background-image: url("data:image/svg+xml,%3csvg width='304px' height='304px' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='304px' height='304px' fill='none' rx='50%' ry='50%' stroke='%23F15641FF' stroke-width='2' stroke-dasharray='6%2c 12%2c 2%2c 1%2c 10%2c 4%2c 18%2c 2%2c 13%2c 8' stroke-dashoffset='1' stroke-linecap='square'/%3e%3c/svg%3e");
  }
  .avatar img {
    max-width: none;
    width: 314px;
  }
}
