.section3 {
  position: relative;
  padding: 90px 102px 0 84px;
}

.sectionHeader {
  position: absolute;
  left: 0;
  top: 50px;
  color: black;
  display: flex;
  align-items: center;
  padding-left: 80px;
  width: 100%;
  bottom: -110px;
  height: 161px;
  background-color: #fff505;
  border-radius: 30px;
}

.sectionHeader span {
  color: #f15641;
}

.sectionHeader span,
.sectionHeader h2 {
  font-size: 40px;
  letter-spacing: 3px;
}

.descriptionAndCats {
  display: flex;
  justify-content: space-between;
}

.groopCats {
  z-index: 2;
}

.description {
  margin-top: 200px;
  max-width: 350px;
  font-family: "PoetsenOne", arial;
}

.description h2 {
  font-size: 32px;
  margin-bottom: 24px;
}
.speed {
  display: flex;
  align-items: center;
  font-family: "Play", sans-serif;
  color: #1ea9f6;
  gap: 15px;
  margin-bottom: 30px;
}

.button {
  margin-top: 57px;
}

.descriptionAndDogs {
  display: flex;
  justify-content: space-between;
}
.descriptionTwo {
  margin: 80px 54px 0 0;
  font-family: "PoetsenOne", arial;
  max-width: 350px;
}
.descriptionTwo h2 {
  font-size: 32px;
  margin-bottom: 24px;
}

.buyNft {
  margin-top: 37px;
}

@media (max-width: 1000px) {
  .sectionHeader {
    height: 120px;
    max-width: 92vw;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  .section3 {
    padding: 90px 40px 0 40px;
  }
  .groopCats img {
    max-width: 432px;
  }
  .description {
    margin-top: 150px;
    margin-left: 60px;
    max-width: 300px;
  }
  .button {
    margin-top: 32px;
  }
  .descriptionAndDogs img {
    max-width: 452px;
  }
  .descriptionAndDogs {
    margin-top: 75px;
  }
  .descriptionTwo {
    margin: 0 0 0 0;
    max-width: 350px;
  }
}
@media (max-width: 860px) {
  .sectionHeader {
    padding-left: 0;
  }
}

@media (max-width: 730px) {
  .descriptionAndCats,
  .descriptionAndDogs {
    flex-direction: column;
    align-items: center;
  }
  .descriptionAndCats {
    margin-top: 50px;
  }
  .sectionHeader span,
  .sectionHeader h2 {
    font-size: 24px;
    margin-bottom: 0;
  }
  .sectionHeader {
    padding-left: 20px;
  }

  .groopCats {
    order: -1;
  }
  .description {
    margin-top: 20px;
  }
}

@media (max-width: 480px) {
  .description {
    margin-top: 40px;
    text-align: center;
    margin-left: 0;
  }
  .sectionHeader {
    height: 80px;
  }
  .descriptionAndCats {
    margin-top: 25px;
  }
  .speed {
    margin-bottom: 16px;
    display: flex;
    justify-content: center;
  }
  .button {
    margin-top: 24px;
  }
  .descriptionAndDogs {
    margin-top: 50px;
  }
  .descriptionTwo {
    margin: 40px 0 0 0;
    text-align: center;
  }
  .descriptionTwo h2 {
    font-size: 32px;
    margin-bottom: 24px;
  }

  .buyNft {
    margin-top: 25px;
    display: flex;
    justify-content: center;
  }
}

@media (max-width: 460px) {
  .sectionHeader {
    height: 60px;
    padding-left: 0;
    justify-content: center;
  }
  .sectionHeader span,
  .sectionHeader h2 {
    font-size: 20px;
  }
  .descriptionAndCats {
    margin-top: 35px;
  }
  .descriptionAndCats img {
    max-width: 319px;
  }
  .descriptionAndDogs img {
    max-width: 320px;
  }
}
@media (max-width: 400px) {
  .description {
    margin-top: 32px;
  }
  .descriptionAndDogs {
    margin-top: 32px;
  }
  .descriptionTwo {
    margin: 32px 0 0 0;
  }
}
